body {
  background-color: rgb(255, 255, 255);
}

.container {
  width: 100%;
  background-size: 1400px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.image {
  width: 700px;
  margin-top: 50px;
  margin: auto;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }
  .image {
    width: 100%;
  }
}
